@import 'common/styles/bongo-vars.scss';

$included-color: rgb(232, 117, 17);
$excluded-color: $bongo-color-blue;

.content-card {
  .content-card-container {
    display: flex;
    flex-direction: column;
    padding: 8px;

    .content-card-container--header {
      display: flex;

      // .content-card-container-header--label {
      //   color: rgba(0, 0, 0, .3);
      //   writing-mode: vertical-lr;
      //   transform: rotate(180deg);
      //   text-align: right;
      //   text-transform: uppercase;
      //   font-weight: bold;
      //   letter-spacing: 1;
      // }
    }
  }

  .content-card-container--banner {
    background-color: rgba(232, 117, 17, .8);
    border-radius: 12px;
    padding: 6px 12px;
    margin-bottom: 12px;
    display: flex;
    align-items: center;

    .content-card-container--banner-label {
      color: white;
      font-weight: bold;
    }
    .content-card-container--banner-icon {
      color: white;
      // font-weight: bold;
      font-size: 24px;
      margin-right: 8px;
    }
  }

  .content-card-container--message {
    // padding: 6px 12px;
    display: flex;
    align-items: center;
    justify-content: center;

    .content-card-container--message-wrapper {
      display: flex;
      border: 2px dashed rgba(0, 0, 0, .5);
      padding: 12px;
      border-radius: 16px;
    }

    .content-card-container--message-label {
      // color: rgba();
      div {
        // font-weight: bold;
        display: flex;
        margin: 8px 0 0 0;
        &:first-child {
          margin: 0;
        }

        .font-icon {
          vertical-align: text-bottom;
        }
      }
    }
    .content-card-container--message-icon {
      // color: white;
      // font-weight: bold;
      font-size: 48px;
      margin-right: 16px;
    }
  }

  .content-card-container--footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 16px 0 0 0;
    justify-content: flex-start;
  }

  .content-card-container--footer-label {
    margin-top: 16px;

    span {
      font-weight: bold;
      font-size: 20px;
      margin: 0 4px;
    }
  }

  .content-card-container--footer-button {
    justify-self: flex-end;
  }

  .content-card-container--included {
    margin-top: 16px;
  }
}

.content-tile-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: stretch;

  .content-tile {
    margin: 8px;
  }
}

.content-tile:hover,
.content-tile:focus,
.content-tile:focus-within {
  &.content-tile-item--excluded {
    outline-style: solid;
    outline-width: 2px;
    outline-offset: 0;
    outline-color: rgba($excluded-color, 1);

    .content-tile-item-footer {
      background-color: rgba($excluded-color, 1);
      .font-icon {
        color: white;
      }
    }
  }
  &.content-tile-item--included {
    outline-style: solid;
    outline-width: 2px;
    outline-offset: 0;
    outline-color: rgba($included-color, 1);

    .content-tile-item-footer {
      background-color: rgba($included-color, 1);
      .font-icon {
        color: white;
      }
    }
  }
}

.content-tile {
  display: flex;
  flex-direction: column;

  width: 175px;
  min-height: 200px;
  background-color: white;
  position: relative;

  transition: outline-color, opacity .3s ease;
  outline-color: transparent;
  opacity: 1;

  &.content-tile-item--flipped {
    .content-tile-item-footer {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: $bongo-color-blue;
    }
  }

  &.new-content-tile {
    // background-color: rgba(0, 0, 0, .1);
    height: auto;

    .new-content-tile-item-main--single-chooser-row {
      // justify-content: center;
      // border-top: 1px solid rgba(0, 0, 0, .2);
    }

    .new-content-tile-item-main--single-button {
      width: 100%;
    }

    &.new-content-tile-item--default {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      justify-content: center;

      .new-content-tile-item--add-content-button {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: center;

        // background-color: rgba(0, 0, 0, .05);
        border-color: rgba(0, 0, 0, .5);
        border-style: dashed;
        border-width: 4px;
        border-radius: 16px;

        &:hover,
        &:focus {
          border-color: $bongo-color-blue;
          background-color: rgba($bongo-color-blue, .12);
        }

        .new-content-tile-item--icon {
          font-size: 72px;
          display: flex;
          align-items: center;
          justify-content: center;
          flex: 1;

          &:hover,
          &:focus {
            color: $bongo-color-blue;
            .new-content-tile-item--text,
            .new-content-tile-item--icon {
              color: $bongo-color-blue;
            }
          }
        }

        .new-content-tile-item--top-text,
        .new-content-tile-item--bottom-text {
          font-size: 18px;
          font-weight: bold;
          letter-spacing: 2px;
          text-transform: uppercase;
          display: flex;
          align-items: center;
          justify-content: center;
          flex: 1;
          text-align: center;
          color: rgba(0, 0, 0, .5);

          &:hover,
          &:focus {
            color: $bongo-color-blue;
          }
        }

        &.md-btn--hover {
          .new-content-tile-item--top-text,
          .new-content-tile-item--bottom-text,
          .new-content-tile-item--icon {
            color: $bongo-color-blue;
          }
        }
      }
    }

    &.new-content-tile-item--chooser {
    }

    .new-content-tile-item-header {
      display: flex;
      align-items: center;
      background-color: $bongo-color-blue;
      color: white;

      .new-content-tile-item-header--label {
        flex: 1;
        padding: 8px;
        margin: 0;
        color: white;
      }

      .new-content-tile-item-header--actions {
        display: flex;

        .button-icon {
          color: white;
        }
      }
    }

    .new-content-tile-item-main {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      flex: 1;

      > button {
        background-color: white;
        margin-bottom: 8px;
      }

      .new-content-tile-item-main--chooser {
        display: flex;
        flex-direction: column;
        flex: 1;

        .new-content-tile-item-main--chooser-row {
          display: flex;
          flex-direction: row;
          flex: 1;

          > div {
            flex: 1;
          }

          .new-content-tile-item-main--chooser-button {
            height: unset;
            flex-direction: column;
            padding: 0;
            margin: 0;

            .font-icon {
              font-size: 32px;
            }
          }
        }
      }
    }

    .new-content-tile-item-footer {
      position: absolute;
      bottom: 0;

      .new-content-tile-item-footer--label {
        text-transform: uppercase;
        font-weight: bold;
        font-size: 20px;
      }
    }
  }

  .content-tile-item-header {
    display: flex;
    align-items: center;
    background-color: white;

    .content-tile-item-header--label {
      &:before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        right: 0;
        background: linear-gradient(-90deg, white, transparent 16px);
      }

      flex: 1;
      color: rgba(0, 0, 0, .5);
      display: inline-block;
      position: relative;
      white-space: nowrap;
      overflow: hidden;
      text-align: left;
      margin-left: 4px;
      font-weight: bold;
      margin-bottom: 0;
    }
    .content-tile-item-header--actions {
      display: flex;
      flex: 0;
      justify-content: flex-end;

      .button-icon {
      }
    }
  }

  .content-tile-item-main {
    display: flex;
    flex-direction: column;
    height: calc(175px * 3 / 4);
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, .5);

    .c-video-container {
      flex: 1;
      margin: 0;

      .video-actions {
        display: none;
      }
    }

    .content-tile-item-type-icon {
      font-size: 72px;

      // color: rgba(0, 0, 0, .2);
      color: white;
    }

    .content-tile-item-type-image {
      object-fit: contain;
      width: 100%;
      height: 100%;
      background-color: white;
    }
  }

  .content-tile-item-details {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex: 1;
    padding: 8px;
  }

  .content-tile-item-actions {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }

  .content-tile-item-footer {
    display: flex;
    // height: 36px;
    align-items: center;
    justify-content: flex-end;
    // border-top: 1px solid rgba(0, 0, 0, .4);
    transition: background-color .5s ease;

    .content-tile-item-footer-button {
      flex: 1;
    }

    &.content-tile-item-footer--included,
    &.content-tile-item-footer--excluded {
      .content-tile-item-footer-button {
        background-color: rgba(0, 0, 0, .05);
        &:hover {
          background-color: rgba(0, 0, 0, .2);
        }
      }
    }

    .content-tile-item-footer--label {
      text-transform: uppercase;
    }
  }
}

.content-list-container {
  .content-list-item--processing {
    .content-list-item-name {
      flex-direction: column;

      .content-list-item-name--desc {
        font-style: italic;
      }

      .content-list-item-name--desc-name {
        font-weight: bold;
      }
    }

    .content-list-item-actions--primary {
      visibility: hidden;
    }
  }

  .content-list-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 4px 8px;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    width: 100%;

    &:hover {
      .content-list-item-actions {
        &.content-list-item-actions--secondary {
          display: flex;
        }
      }
    }

    &:not(:hover) {
      .content-list-item-actions--button-done {
        box-shadow: none;
      }
    }

    .content-list-item-actions--include-exclude {
      cursor: pointer;
      display: flex;

      .font-icon {
        font-size: 24px;
      }
    }

    &.content-list-item--excluded {
      .content-list-item-actions--include {
        cursor: pointer;
      }

      &:hover {
        .content-list-item-actions--button-done {
          background-color: rgba($excluded-color, 1);
          .font-icon {
            color: white;
          }
        }
      }
    }

    &.content-list-item--included {
      &:hover {
        .content-list-item-actions--button-done {
          background-color: rgba($included-color, 1);
          .font-icon {
            color: white;
          }
        }
      }
    }

    > * {
      margin-left: 8px;
      &:first-child {
        margin-left: 0;
      }
    }

    .content-list-item-icon {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;

      // .content-list-item-drag-icon {
      //   font-size: 24px;
      // }

      .content-list-item-type-icon {
        color: rgba(0, 0, 0, .5);
        font-size: 32px;
      }

      // .content-list-item-type-image {
      //   object-fit: contain;
      //   width: 100%;
      //   height: 100%;
      // }
    }

    .content-list-item-name {
      display: flex;
      flex: 1;
      overflow: hidden;

      .content-list-item-name--label {
        flex: 1;
        color: rgba(0, 0, 0, .5);
        display: inline-block;
        position: relative;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        text-align: left;
        // margin-left: 4px;
        font-weight: bold;
        margin-bottom: 0;
      }
    }

    .content-list-item-actions {
      display: flex;
      flex-direction: row;
      align-items: center;

      .content-list-item-actions--primary {
      }

      &.content-list-item-actions--secondary {
        // display: none;
      }

      .content-list-item-actions--button-done {
        height: 40px;
        width: 40px;
        min-width: 40px;
        border-radius: 8px;
        margin: 0;
        padding: 0;

        .font-icon {
          // color: rgba(0, 0, 0, .5);
        }
      }

      > * {
        margin-right: 8px;

        &:last-child {
          margin-right: 0;
        }
      }

      .item-sep {
        width: 2px;
        background-color: rgba(0, 0, 0, .2);
        align-self: stretch;
        margin: 4px 0;
      }
    }

    &.new-content-list {
      padding: 0;
      border: 0;
      margin-bottom: 8px;
      &:hover {
        background: none;
        cursor: auto;
      }

      &.new-content-list-item--default {
        display: flex;
        flex-direction: row;
        align-items: center;
        // justify-content: center;

        .new-content-list-item--add-content-button {
          flex: 1;
          display: flex;
          flex-direction: row;
          align-items: center;
          height: auto;

          // justify-content: center;

          // background-color: rgba(0, 0, 0, .05);
          border-color: rgba(0, 0, 0, .5);
          border-style: dashed;
          border-width: 4px;
          border-radius: 16px;

          &:hover,
          &:focus {
            border-color: $bongo-color-blue;
            background-color: rgba($bongo-color-blue, .12);

            .new-content-list-item--text,
            .new-content-list-item--icon {
              color: $bongo-color-blue;
            }
          }

          .new-content-list-item--icon {
            font-size: 48px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 16px;
            // flex: 1;
            &:hover,
            &:focus {
              color: $bongo-color-blue;
            }
          }

          .new-content-list-item--text {
            font-size: 18px;
            font-weight: bold;
            letter-spacing: 2px;
            text-transform: uppercase;
            display: flex;
            align-items: center;
            justify-content: center;
            // flex: 1;
            text-align: center;
            color: rgba(0, 0, 0, .5);
            &:hover,
            &:focus {
              color: $bongo-color-blue;
            }
          }

          &.md-btn--hover {
            .new-content-list-item--text,
            .new-content-list-item--icon {
              color: $bongo-color-blue;
            }
          }
        }
      }

      &.new-content-list-item--chooser {
        display: flex;
        flex-direction: column;
        align-items: stretch;
      }

      .new-content-list-item-header {
        display: flex;
        align-items: center;
        background-color: #2679c7;
        color: white;

        // background: rgba(0, 0, 0, .1);
        .new-content-list-item-header--label {
          flex: 1;

          // display: flex;
          // align-items: center;
          // font-size: 16px;
          // font-weight: bold;
          // text-transform: uppercase;
          padding: 8px;

          // color: rgba(0, 0, 0, .54);
          margin: 0;
          color: white;
        }

        .new-content-list-item-header--actions {
          display: flex;

          // flex: 1;
          // justify-content: flex-end;

          .button-icon {
            color: white;
          }
        }
      }

      .new-content-list-item-main {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        flex: 1;

        > button {
          background-color: white;
          margin-bottom: 8px;
        }

        .new-content-list-item-main--chooser {
          display: flex;
          flex-direction: column;
          flex: 1;

          .new-content-list-item-main--chooser-row {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            flex: 1;

            .new-content-list-item-main--chooser-button {
              height: unset;
              flex-direction: column;
              padding: 8px;
              margin: 0;
              flex: 1;
              max-width: 150px;

              .font-icon {
                font-size: 32px;
              }
            }
          }
        }
      }

      .new-content-list-item-footer {
        position: absolute;
        bottom: 0;

        .new-content-list-item-footer--label {
          text-transform: uppercase;
          font-weight: bold;
          font-size: 20px;
        }
      }
    }
  }
}
