.dev-container {
  margin: 0;
  width: 100vw;
  height: 100vh;
  display: flex;

  .dev-button {
    display: flex;
    align-items: center;
    padding: 4px;
    height: auto;
    width: auto;
    min-width: 0;
    font-size: 12px;
    margin: 0;
    white-space: nowrap;
    font-weight: bold;

    .md-icon {
      line-height: unset;
    }
  }

  .dev-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
  }

  .dev-top {
    padding: 4px;
    background-color: #fff4b7;
    border-bottom: 2px solid black;
    color: black;
    display: flex;
    flex-direction: column;

    .dev-path-container {
      display: flex;
      flex-direction: row;
      align-items: center;

      > * {
        margin-right: 4px;
      }

      > *:last-child {
        margin-right: 0;
      }

      .dev-path-text {
        // max-width: 500px;
        flex: 1;
        border: 1px solid rgba(0, 0, 0, .5);
        padding: 0 4px;

        &.dev-path-text--dirty {
          background-color: yellow;
        }

        .md-text-field--margin {
          margin: 0;
        }

        .md-divider {
          background-color: black;
        }

        .md-divider--text-field {
          display: none;
          margin: 0;
        }

        .md-text-field {
          height: 20px;
          font-size: 12px;
        }
      }
    }

    .dev-settings-container {
      display: flex;
      justify-content: center;
      flex: 1;
      position: relative;
     
      > .dev-settings-button {
        margin: 0 8px;
      }
    }

    .dev-settings-config-container {
      position: absolute;
      border: 1px solid black;
      background-color: white;
      padding: 8px;
      top: 24px;
      display: flex;
      flex-direction: column;
      align-items: stretch;
      justify-content: flex-start;
      max-width: 100%;

      > * {
        margin-bottom: 8px;
      }

      > *:last-child {
        margin-bottom: 0;
      }
    }

    .dev-settings-config-text {
      width: 500px;
      max-width: 100%;
      border: 1px solid rgba(0, 0, 0, .5);
      padding: 0;

      textarea {
        font-family: monospace;
      }

      .md-text-field--margin {
        margin: 0;
      }

      .md-divider {
        background-color: black;
      }

      .md-divider--text-field {
        display: none;
        margin: 0;
      }
    }

    .dev-settings-config-bottom {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-end;
    }
  }

  .dev-iframe-container {
    /* Brightspace styles */
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    background: transparent;

    /****/

    display: flex;
    width: 100%;
    flex: 1;
  }

  .dev-iframe {
    /* Brightspace styles */
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    background: transparent;
    width: 100%;

    /****/

    width: 1px;
    min-width: 100%;
  }
}
